import React,{useState,useEffect} from 'react'
import { Link, useNavigate } from 'react-router-dom';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Container from '@mui/material/Container';
import Paper from '@mui/material/Paper';
import LinearProgress from '@mui/material/LinearProgress';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Slider } from '@mui/material';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import CircularProgress from '@mui/material/CircularProgress'
import { server_home } from '../secret/secret';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Avatar from '@mui/material/Avatar';

const theme = createTheme();
class Complaint{
  constructor(id,topic,description,status,category,availability){
    this.id = id;
    this.topic = topic;
    this.description = description;
    this.status = status;
    this.category = category;
    this.availability = availability;
  }
}
const categories = ['Hygiene','electricity','food','services','other','all'];
const times = ['12 AM','01 AM','02 AM','03 AM','04 AM','05 AM','06 AM','07 AM','08 AM','09 AM','10 AM','11 AM','12 PM'
,'01 PM','02 PM','03 PM','04 PM','05 PM','06 PM','07 PM','08 PM','09 PM','10 PM','11 PM'];
const hostel_list = ["Kedar","Badri","Rudra","Neelkanth","Alaknanda"];

function AdminHome() {
  const [expanded, setExpanded] = useState(false);
  const [showAlert,setShowAlert] = useState(false);
  const [complaintList,setComplaintList] = useState([]);
  const [fixedComplaintList,setFixedComplaintList] = useState([]);
  const [name,setName] = useState("");
  const [rank,setRank] = useState("");
  const [category,setCategory] = useState("");
  const [status,setStatus] = useState("");
  const [availability,setAvailability] = useState("");
  const [queryStatus,setQueryStatus] = useState("");
  const [hostel,setHostel] = useState("");
  const navigate = useNavigate();

  function findVal(str){
    if (str==="officer1")return 1;
    else if (str==="officer2")return 2;
    else if (str==="officer3")return 3;
    return 4;
  }
  async function getAllComplaints(temp_rank){
    const response  = await fetch(`${server_home}/admin/allcomplaints`,{
      method:'GET',
      headers:{
          'Content-Type':'application/json',
      },
      credentials: 'include'
    })
    const data = response.json();
    data.then(res=>{
      if (res.status==="ok"){
        console.log(res.data);
        let complaint_list = res.data;
        //console.log(temp_rank);
        let arr = new Array();

        for (let i=0;i<complaint_list.length;i++){
          //console.log(findVal(complaint_list[i]['status'])>=findVal(temp_rank));
          if (findVal(complaint_list[i]['status'])>=findVal(temp_rank)){
            //console.log(findVal(complaint_list[i]['status']),findVal(temp_rank));
            const temp_obj = new Complaint(complaint_list[i]['_id'],complaint_list[i]['topic'],complaint_list[i]['description'],complaint_list[i]['status'],
              complaint_list[i]['category'],complaint_list[i]['availability']);
              arr.push(temp_obj);
              //console.log("before arr");
          }
        }
        console.log(arr);
        setComplaintList(arr);
        setFixedComplaintList(arr);
      }else{
        // try login again
        let officer_id = localStorage.getItem('');
        let password = localStorage.getItem('');
        if (officer_id && password){
          tryLogin(officer_id,password);
        }else{
          setShowAlert(true);
        }
      }
    })
  }
  
  
  async function tryLogin(officer_id,password){
    const response = await fetch(`${server_home}/admin/login/`,{
        method:'POST',
        headers:{
            'Content-Type':'application/json',
        },
        body:JSON.stringify({
            officerId:officer_id,
            password:password
        }),
        credentials:'include',
    })
    const data = response.json();
    data.then(res=>{
        console.log(res);
        if (res.status==="ok"){
            localStorage.setItem('officer_id',res.data.officerId);
            localStorage.setItem('officer_password',res.data.password);
            getData();
        }else{
            //alert(res.message);
            setShowAlert(true);
        }
    })
  };
  
  
  
  async function getData(){
    const response = await fetch(`${server_home}/admin/myprofile`,{
      method:'GET',
      headers:{
          'Content-Type':'application/json',
      },
      credentials: 'include'
    })
    const data = response.json();
    data.then(res=>{
      if (res.status==="ok"){
        setName(res.data.name);
        setRank(res.data.rank);
        getAllComplaints(res.data.rank);
      }else{
        // try login again
        let officer_id = localStorage.getItem('officer_id');
        let password = localStorage.getItem('officer_password');
        if (officer_id && password){
          tryLogin(officer_id,password);
        }else{
          setShowAlert(true);
        }
      }
    })
  }
  useEffect(()=>{
    getData();
    //getAllComplaints(); 
  },[])

  async function updateComplaint(id,str){
    const response  = await fetch(`${server_home}/admin/updateComplaintStatus/${id}`,{
      method:'PATCH',
      headers:{
          'Content-Type':'application/json',
      },
      body:JSON.stringify({
        'status':str
      }),
      credentials: 'include'
    })
    const data = response.json();
    data.then(res=>{
      console.log(res);
      if (res.status==="ok"){
        getAllComplaints();
      }
    })
  }
  async function escalateComplaint(id){
    const response  = await fetch(`${server_home}/admin/escalateComplaint/${id}`,{
      method:'PATCH',
      headers:{
          'Content-Type':'application/json',
      },
      credentials: 'include'
    })
    const data = response.json();
    data.then(res=>{
      console.log(res);
      if (res.status==="ok"){
        getAllComplaints();
      }
    })
  }
  async function logoutClicked(){
    const response = await fetch(`${server_home}/admin/logout`,{
      method:'GET',
      headers:{
          'Content-Type':'application/json',
      },
      credentials: 'include'
    })
    const data = response.json();
    data.then(res=>{
      //console.log(res);
      localStorage.removeItem('my_role');
      localStorage.removeItem('officer_id');
      localStorage.removeItem('officer_password');
      navigate('/');
    })
  }
  const handleAlertClose = (event,reason) => {
    if (reason && reason == "backdropClick") return;
    setShowAlert(false);
    navigate('/admin_login');
  };
  const handleExpanded = (panel) => (event, isExpanded) => {
    if (isExpanded){
      setExpanded(panel);
    }else{
      setExpanded(false);
    }
  };
  
  function getStatusColor(stat){
    if (stat==="accepted")return "green";
    return "red";
  }
  function getAval(str){
    let idx = str.indexOf('-')
    let start = parseInt(str.substring(0,idx));
    let end = parseInt(str.substring(idx+1));
    if (start==end) return "anytime";
    return times[start]+" to "+times[end];
  }
  function categoryCheck(complain){
    //console.log(category,categories[complain['category']]);
    if (category=="" || category=="all")return true;
    if (categories[complain['category']]==category)return true;
    console.log("category check fails")
    return false;
  }
  function statusCheck(complain){
    //return true;
    console.log(queryStatus,complain['status'])
    if (queryStatus=="")return true;
    if (complain['status']==rank && queryStatus=="pending")return true;
    console.log("status check fails")
    return false;
  }
  function availabilityCheck(complain){
    //return true;
    if (availability=="")return true;
    let str = complain['availability'];
    let idx = str.indexOf('-')
    let start = parseInt(str.substring(0,idx));
    let end = parseInt(str.substring(idx+1));
    if (start==end)return true;
    if (end<start){
      if (availability<=end)return true;
      if (availability>=start)return true;
    }
    else{
      if(availability>=start && availability<=end)return true;
    }
    console.log("availability check fails")
    return false;
  }
  function applyFilters(){
    let arr = new Array();
    for (let i=0;i<fixedComplaintList.length;i++){
      console.log(fixedComplaintList[i]);
      if (categoryCheck(fixedComplaintList[i]) && statusCheck(fixedComplaintList[i]) && availabilityCheck(fixedComplaintList[i]))
        arr.push(fixedComplaintList[i]);
    }
    setComplaintList(arr);
  }
  function resetFilters(){
    setCategory("");
    setAvailability("");
    setHostel("");
    setQueryStatus("");
    //applyFilters();
    setComplaintList(fixedComplaintList);
  }
  return (
    <ThemeProvider theme={theme}>
      <Dialog
        fullWidth={true}
        open={showAlert}
        onClose={handleAlertClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Please Login!
        </DialogTitle>
        <DialogActions>
          <Button onClick={handleAlertClose}>Login</Button>
        </DialogActions>
      </Dialog>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>Hostel Management Portal</Typography>
          <Button color='inherit' onClick={logoutClicked}>logout</Button>
        </Toolbar>
      </AppBar>
      <Box display="flex" justifyContent="center">
        <Typography sx={{margin:"20px"}} variant='h6'>{name} ({rank})</Typography>
      </Box>
      <Container component="main" maxWidth="xs">
        <Box sx={{marginTop:3,display: 'flex',flexDirection: 'column',alignItems: 'center',}}>
          <FormControl sx={{margin:"10px"}} fullWidth>
            <InputLabel id="demo-simple-select-label">Category</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              value={category}
              label="Category"
              onChange={(e)=>setCategory(e.target.value)}
            >
              {
                categories.map((category,index)=>{
                  return <MenuItem value={category}>{category}</MenuItem>
                })
              }
            </Select>
          </FormControl>
          <FormControl sx={{margin:"10px"}} fullWidth>
            <InputLabel id="demo-simple-select-label">Status</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              value={queryStatus}
              label="Status"
              onChange={(e)=>setQueryStatus(e.target.value)}
            >
              <MenuItem value="accepted">accepted</MenuItem>
              <MenuItem value="rejected">rejected</MenuItem>
              <MenuItem value="pending">pending</MenuItem>
            </Select>
          </FormControl>
          <FormControl sx={{margin:"10px"}} fullWidth>
            <InputLabel id="demo-simple-select-label">Available</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              value={availability}
              label="Available"
              onChange={(e)=>setAvailability(e.target.value)}
            >
              {
                times.map((value,idx)=>(
                  <MenuItem value={idx}>{value}</MenuItem>
                ))
              }
            </Select>
          </FormControl>
          <FormControl sx={{margin:"10px"}} fullWidth>
            <Box sx={{display:"flex",justifyContent:"space-evenly"}}>
              <Button onClick={applyFilters}>Apply</Button>
              <Button onClick={resetFilters}>Reset</Button>
            </Box>
          </FormControl>
        </Box>
      </Container>
      <Container component="main" maxWidth="md">
        <Box sx={{marginTop:3,display: 'flex',flexDirection: 'column',alignItems: 'center',}}>
          {
            complaintList.map((ele,index)=>(
              <Paper sx={{width:"100%",margin:"10px 0px 10px 0px"}} elevation={3}>
                <Accordion expanded={expanded === ele.id} onChange={handleExpanded(ele.id)}
                  sx={{
                      '& .MuiAccordionSummary-root':{
                        paddingLeft:"10px"    
                      }
                    }}
                >
                  <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel1a-content" id="panel1a-header"
                    sx={{
                      '& .MuiAccordionSummary-expandIconWrapper':{
                        position:'absolute',right:'10px',bottom:'10px',top:"auto",left:"auto"
                      },
                      '& .MuiAccordionSummary-content':{
                        display: 'inline-table'
                      }
                    }}
                  >
                    <Box sx={{ display: 'flex',justifyContent: "space-between", width: '80%',margin:"5px 0px 0px 0px" }}>
                      <Typography fontWeight="600" fontSize={14} variant="h6" component="h2">{ele.topic}</Typography>
                      <Typography  fontWeight="600" fontSize={14} variant="h6" component="h2">
                        {categories[ele.category]}
                      </Typography>
                    </Box>
                  </AccordionSummary>
                  <AccordionDetails>
                    <Box sx={{ display: 'flex',flexDirection: 'column',alignItems: 'center'}}>
                      <Typography variant="h7" sx={{fontWeight:"900"}}>available: {getAval(ele.availability)}</Typography>
                      <Typography variant='h7'>{ele.description}</Typography>
                    </Box>
                    {/* <Box sx={{ display: 'flex',flexDirection:'column'}}>
                    <Typography fontWeight="900" variant='h7'>details:</Typography>
                      <Typography fontWeight="900" variant='h7'>name:{ele.student['name']}</Typography>
                      <Typography fontWeight="900" variant='h7'>email:{ele.student['email']}</Typography>
                      <Typography fontWeight="900" variant='h7'>hostel:{ele.student['hostelName']}</Typography>
                      <Typography fontWeight="900" variant='h7'>room number: {ele.student['roomNumber']}</Typography>
                      <Typography fontWeight="900" variant='h7'>contact number: {ele.student['contactNumber']}</Typography>
                    </Box> */}
                    {
                      (ele.status==="accepted" || ele.status==="rejected") ? (
                        <Box sx={{ display: 'flex',flexDirection: 'column',alignItems: 'center'}}>
                          <Typography color={getStatusColor(ele.status)} variant='h6'>{ele.status}</Typography>
                        </Box>
                      ):(
                        findVal(rank)==findVal(ele.status) ? (
                          <Box sx={{marginTop:"30px",display:"flex",justifyContent:"space-evenly"}}>
                            <Button onClick={()=>escalateComplaint(ele.id)} sx={{display:`${ele.status==="officer3"?"none":""}`}} color='warning' variant="contained">Escalate</Button>
                            <Button color='success' variant="contained" onClick={()=>updateComplaint(ele.id,"accepted")}>Accept</Button>
                            <Button color='error' variant="contained"  onClick={()=>updateComplaint(ele.id,"rejected")}>Reject</Button>
                          </Box>
                        ):(
                          <Box sx={{ display: 'flex',flexDirection: 'column',alignItems: 'center'}}>
                            <Typography color="orange" variant='h6'>pending...</Typography>
                          </Box>
                        )
                      )
                    }
                  </AccordionDetails>
                </Accordion>
              </Paper>
            ))
          }
        </Box>
      </Container>
      <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
    </ThemeProvider>
  )
}

export default AdminHome
