import React, { useEffect, useState } from 'react'
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Box from '@mui/material/Box';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import Avatar from '@mui/material/Avatar';
import { Paper, Typography,Grid, CircularProgress, Button } from '@mui/material';
import { server_home } from '../secret/secret';
import { useNavigate,Link } from 'react-router-dom';

const theme = createTheme();

function StudentProfile() {
  const [name,setName] = useState("");
  const [email,setEmail] = useState("");
  const [studentId,setStudentId] = useState("");
  const [hostelName,setHostelName] = useState("");
  const [roomNumber,setRoomNumber] = useState("");
  const [contactNumber,setContactNumber] = useState("");
  const [myComplaintsList,setMyComplaintList] = useState([]);

  const navigate = useNavigate();
  

  async function getStudentData(){
    const response = await fetch(`${server_home}/student/myprofile`,{
      method:'GET',
      headers:{
          'Content-Type':'application/json',
      },
      credentials: 'include'
    })
    const data = response.json();
    data.then(res=>{
      console.log(res);
      if (res.status==="ok"){
        setName(res.data.name);
        setEmail(res.data.email);
        setStudentId(res.data.studentId);
        setHostelName(res.data.hostelName);
        setRoomNumber(res.data.roomNumber);
        setContactNumber(res.data.contactNumber);
        setMyComplaintList(res.data.my_complaints);
      }else{
        // show something went wrong
      }
    })
  }
  async function logoutClicked(){
    const response = await fetch(`${server_home}/student/logout`,{
      method:'GET',
      headers:{
          'Content-Type':'application/json',
      },
      credentials: 'include'
    })
    const data = response.json();
    data.then(res=>{
      //console.log(res);
      localStorage.removeItem('student_email');
      localStorage.removeItem('student_password');
      navigate('/');
    })
  }
  function goToHome(){
    navigate('/student_home')
  }
  useEffect(()=>{
    getStudentData();
  },[])
  return (
    <ThemeProvider theme={theme}>
      {
        name==''?(
          <Box sx={{ display: 'flex',alignItems:"center",justifyContent:"center",minHeight:"100vh" }}>
              <CircularProgress />
          </Box>
        ):(
          <Box sx={{marginTop:5,display: 'flex',flexDirection: 'column',alignItems: 'center',}}>
            <Avatar sx={{ width: 56, height: 56 }}/>
            <Typography sx={{margin:"20px"}} variant='h4'>{name}</Typography>
            <Paper sx={{width:"80%"}}>
              <Box sx={{margin:2}}>
                <Grid container rowSpacing={3} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
                  <Grid display="flex" flexDirection="column" alignItems="center" item xs={6}>Email</Grid>
                  <Grid display="flex" flexDirection="column" alignItems="center"  item xs={6}>{email}</Grid>
                  <Grid display="flex" flexDirection="column" alignItems="center"  item xs={6}>Student Id</Grid>
                  <Grid display="flex" flexDirection="column" alignItems="center"  item xs={6}>{studentId}</Grid>
                  <Grid display="flex" flexDirection="column" alignItems="center"  item xs={6}>Hostel Name</Grid>
                  <Grid display="flex" flexDirection="column" alignItems="center"  item xs={6}>{hostelName}</Grid>
                  <Grid display="flex" flexDirection="column" alignItems="center"  item xs={6}>Room Number</Grid>
                  <Grid display="flex" flexDirection="column" alignItems="center"  item xs={6}>{roomNumber}</Grid>
                  <Grid display="flex" flexDirection="column" alignItems="center"  item xs={6}>Contact Number</Grid>
                  <Grid display="flex" flexDirection="column" alignItems="center"  item xs={6}>{contactNumber}</Grid>
                  <Grid display="flex" flexDirection="column" alignItems="center"  item xs={6}>Complaints posted</Grid>
                  <Grid display="flex" flexDirection="column" alignItems="center"  item xs={6}>{myComplaintsList.length}</Grid>
                </Grid>
              </Box>
            </Paper>
            <Button variant='contained' onClick={logoutClicked} sx={{margin:"20px"}}>Logout</Button>
            <Button variant='contained' onClick={goToHome} sx={{margin:"0px"}}>Home</Button>
          </Box>
        )
      }
      
    </ThemeProvider>
  )
}

export default StudentProfile