import React,{useState,useEffect} from 'react'
import { Link, useNavigate } from 'react-router-dom';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Container from '@mui/material/Container';
import Paper from '@mui/material/Paper';
import LinearProgress from '@mui/material/LinearProgress';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Slider } from '@mui/material';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import CircularProgress from '@mui/material/CircularProgress'
import { server_home } from '../secret/secret';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Avatar from '@mui/material/Avatar';

const theme = createTheme();
class Complaint{
  constructor(id,topic,description,status,category,availability){
    this.id = id;
    this.topic = topic;
    this.description = description;
    this.status = status;
    this.category = category;
    this.availability = availability;
  }
}

function StudentHome() {
  const [expanded, setExpanded] = useState(false);
  const [showAlert,setShowAlert] = useState(false);
  const [myComplaintList,setMyComplaintList] = useState([]);

  const navigate = useNavigate();
  const handleAlertClose = (event,reason) => {
    if (reason && reason == "backdropClick") return;
    setShowAlert(false);
    navigate('/student_login');
  };

  async function tryLogin(email,password){
    const response = await fetch(`${server_home}/student/login/`,{
        method:'POST',
        headers:{
            'Content-Type':'application/json',
        },
        body:JSON.stringify({
            officerId:email,
            password:password
        }),
        credentials:'include',
    })
    const data = response.json();
    data.then(res=>{
        console.log(res);
        if (res.status==="ok"){
            localStorage.setItem('student_email',res.data.email);
            localStorage.setItem('student_password',res.data.password);
            getMyComplaints();
        }else{
            //alert(res.message);
            setShowAlert(true);
        }
    })
  };
  function goToMyProfile(){
    // navigate to /student_profile
    navigate('/student_profile')
  }

  function addComplaintClicked(){
    // navigate to /add complaint
    navigate('/add_complaint');
  }
  const handleExpanded = (panel) => (event, isExpanded) => {
    if (isExpanded){
      setExpanded(panel);
    }else{
      setExpanded(false);
    }
  };
  async function getMyComplaints(){
    //console.log("called");
    const response = await fetch(`${server_home}/student/myprofile`,{
      method:'GET',
      headers:{
          'Content-Type':'application/json',
      },
      credentials: 'include'
    })
    //console.log("yo")
    const data =response.json();
    //console.log(data);
    data.then(res=>{
      console.log(res);
      if (res.status=="ok"){
        let complaint_list = res.data.my_complaints;
        console.log(complaint_list[0]);
        let arr = new Array();
        for (let i=0;i<complaint_list.length;i++){
          const temp_obj = new Complaint(complaint_list[i]['_id'],complaint_list[i]['topic'],complaint_list[i]['description'],complaint_list[i]['status'],
            complaint_list[i]['category'],complaint_list[i]['availability']);
          arr.push(temp_obj);
        }
        setMyComplaintList(arr);
      }
      else{
        //console.log("yo");
        let my_email = localStorage.getItem('student_email');
        let my_password = localStorage.getItem('student_password');
        tryLogin(my_email,my_password);
      }
    })
  }
  function getStatus(temp){
    if (temp==="accepted" || temp==="rejected") return temp;
    return "pending...";
  }
  function getStatusColor(temp){
    if (temp==="accepted")return "green";
    if (temp==="rejected")return "red";
    return "orange";
  }
  useEffect(()=>{
    getMyComplaints();
    //console.log("hello")
  },[])

  return (
    <ThemeProvider theme={theme}>
      <Dialog
        fullWidth={true}
        open={showAlert}
        onClose={handleAlertClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Please Login!
        </DialogTitle>
        <DialogActions>
          <Button onClick={handleAlertClose}>Login</Button>
        </DialogActions>
      </Dialog>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h7" component="div" sx={{ flexGrow: 1 }}>Hostel Management Portal</Typography>
          <Button onClick={goToMyProfile}><Avatar src="/static/images/avatar/1.jpg" /></Button>
        </Toolbar>
      </AppBar>
      <Box display="flex" justifyContent="center">
        <Button variant="contained" onClick={addComplaintClicked} sx={{margin:"50px"}} endIcon={<AddIcon />}>
          <Typography variant="h7" component="div" sx={{ margin:"10px" }}>Add Complaint</Typography>
        </Button>
      </Box>
      <Box display="flex" justifyContent="center" alignItem="center">
        <Typography variant="h6">Previous Complaints:</Typography>
      </Box>
      <Container component="main" maxWidth="md">
        <Box sx={{marginTop:3,display: 'flex',flexDirection: 'column',alignItems: 'center',}}>
        {
          myComplaintList.map(ele=>(
            <Paper sx={{width:"100%",margin:"10px 0px 10px 0px"}} elevation={3}>
              <Accordion expanded={expanded === ele.id} onChange={handleExpanded(ele.id)}
                sx={{
                    '& .MuiAccordionSummary-root':{
                      paddingLeft:"10px"    
                    }
                  }}
                  >
                  <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel1a-content" id="panel1a-header"
                    sx={{
                      '& .MuiAccordionSummary-expandIconWrapper':{
                        position:'absolute',right:'10px',bottom:'10px',top:"auto",left:"auto"
                      },
                      '& .MuiAccordionSummary-content':{
                        display: 'inline-table'
                      }
                    }}
                  >
                    <Box sx={{ display: 'flex',justifyContent: "space-between", width: '80%',margin:"5px 0px 0px 0px" }}>
                      <Typography fontWeight="600" fontSize={14} variant="h6" component="h2">{ele.topic}</Typography>
                      <Typography color={getStatusColor(ele.status)} fontWeight="600" fontSize={14} variant="h6" component="h2">
                        {getStatus(ele.status)}
                      </Typography>
                    </Box>
                  </AccordionSummary>
                  <AccordionDetails>
                    <Box sx={{ display: 'flex',flexDirection: 'column',alignItems: 'center'}}>
                      <Typography variant='h7'>{ele.description}</Typography>
                    </Box>
                  </AccordionDetails>
              </Accordion>
            </Paper>
          ))
        }
        
        </Box>
      </Container>
    </ThemeProvider>
  )
}

export default StudentHome
