import React,{useState,useEffect} from 'react'
import { useNavigate,Link } from 'react-router-dom';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import AddIcon from '@mui/icons-material/Add';
import { server_home } from '../secret/secret';
import Alert from '@mui/material/Alert';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import { StepLabel, Stepper } from '@mui/material';
import Step from '@mui/material/Step';
import InputLabel from '@mui/material/InputLabel';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';

const theme = createTheme();
const steps = ['Title', 'Description', 'Category','Availability'];
const categories = ['Hygiene','electricity','food','services','other'];
const times = ['12 AM','01 AM','02 AM','03 AM','04 AM','05 AM','06 AM','07 AM','08 AM','09 AM','10 AM','11 AM','12 PM'
,'01 PM','02 PM','03 PM','04 PM','05 PM','06 PM','07 PM','08 PM','09 PM','10 PM','11 PM'];

function AddComplaint() {
  const [showAlert,setShowAlert] = useState(false);
  const [alertMessage,setAlertMessage] = useState("");
  const [activeStep, setActiveStep] = React.useState(0);
  const [title,setTitle] = React.useState("");
  const [description,setDescription] = useState("");
  const [availability,setAvailability] = useState("");
  const [category,setCategory] = useState("");
  const [startTime,setStartTime] = useState(0);
  const [endTime,setEndTime] = useState(0);
  const navigate = useNavigate();

  async function addComplaint(){
    const response = await fetch(`${server_home}/student/addcomplaint`,{
      method:'POST',
        headers:{
            'Content-Type':'application/json',
        },
        body:JSON.stringify({
          topic:title,
          description:description,
          category:category,
          availability:startTime+'-'+endTime
        }),
        credentials:'include',
    })
    const data = response.json();
    data.then(res=>{
      console.log(res);
      if (res.status==="ok"){
        // navigate to student_home
        navigate('/student_home');
      }else{
        console.log(res.message);
      }
    })
  }
  function handleBack(){
    if (activeStep===0){
      navigate('/student_home');
    }
    else setActiveStep((prevActiveStep) => prevActiveStep - 1);
  }
  function handleNext(){
    if (activeStep===3){
      // show confirm box
      setAvailability(startTime+'-'+endTime);
      addComplaint();
    }
    else if (activeStep===0){
      if (title===""){
        alert("Please provide the title");
      }else{
        setActiveStep((prevActiveStep)=>prevActiveStep+1);
      }
    }
    else if (activeStep===1){
      if (description===""){
        alert("Please provide description");
      }else{
        setActiveStep((prevActiveStep)=>prevActiveStep+1);
      }
    }
    else{
      if (category===""){
        alert("please select a category");
      }else{
        setActiveStep((prevActiveStep)=>prevActiveStep+1);
      }
    }
  }
  return (
    <ThemeProvider theme={theme}>
          <Collapse sx={{width:"100%"}} in={showAlert}>
            <Alert 
              action={
                  <IconButton
                      size="small" color="inherit"
                      onClick={() => {
                          setShowAlert(false);
                      }}
                  >
                      <CloseIcon fontSize="inherit" />
                  </IconButton>
              }
              severity="warning">
              {alertMessage}
            </Alert>
          </Collapse>
            <Box
                sx={{
                  width:"100%",
                  marginTop: 4,
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                }}
              >
                <Avatar sx={{ bgcolor: 'primary.main' }}>
                  <AddIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                  Add Complaint
                </Typography>
              </Box>
          <Box sx={{ margin: '50px' }}>
            <Stepper activeStep={activeStep}>
              {
                steps.map((label, index) => {
                  const stepProps = {};
                  const labelProps = {};
                  return (
                      <Step key={label} {...stepProps}>
                          <StepLabel {...labelProps}>{label}</StepLabel>
                      </Step>
                  );
                })
              }
            </Stepper>
            {
              <React.Fragment>
              {
                activeStep===0 ? (
                  <Box sx={{ marginTop:'80px',display:'flex',flexDirection:'column',justifyContent:'center',alignItems:'center' }}>
                    <TextField
                        size='Normal'
                        label="Complaint Title"
                        value={title || ''}
                        focus={true}
                        onChange={(e)=>setTitle(e.target.value)}
                    />
                  </Box>
                ) :(
                  activeStep==1 ? (
                    <Box sx={{ marginTop:'80px',display:'flex',flexDirection:'column',justifyContent:'center',alignItems:'center' }}>
                      <TextField
                          multiline
                          fullWidth
                          size='Normal'
                          label="Complaint Description"
                          value={description || ''}
                          focus={true}
                          onChange={(e)=>setDescription(e.target.value)}
                      />
                    </Box>
                  ) : (
                    activeStep==2 ? (
                      <Box sx={{ marginTop:'80px',display:'flex',flexDirection:'column',justifyContent:'center',alignItems:'center' }}>
                        <FormControl fullWidth>
                        <InputLabel id="demo-simple-select-label">Category</InputLabel>
                        <Select
                          labelId="demo-simple-select-label"
                          value={category}
                          label="Category"
                          onChange={(e)=>setCategory(e.target.value)}
                        >
                          {
                            categories.map((category,index)=>{
                              return <MenuItem value={index}>{category}</MenuItem>
                            })
                          }
                        </Select>
                        </FormControl>
                      </Box>
                    ) : (
                      <Box sx={{ marginTop:'80px',display:'flex',flexDirection:'column',justifyContent:'center',alignItems:'center' }}>
                        <Box sx={{display:'flex'}}>
                          <Box sx={{margin: "auto",marginRight:3}}>
                          <Typography variant='h5'>from</Typography>
                          </Box>
                          <Select sx={{width:"150px"}} value={startTime} onChange={(e)=>setStartTime(e.target.value)}>
                              {
                                times.map((time,index)=>{
                                  return <MenuItem value={index}>{time}</MenuItem>
                                })
                              }
                          </Select>
                          <Box sx={{margin: "auto",marginLeft:3,marginRight:3}}>
                            <Typography variant='h5'>to</Typography>
                          </Box>
                          <Select sx={{width:"150px"}} value={endTime} onChange={(e)=>setEndTime(e.target.value)}>
                              {
                                times.map((time,index)=>{
                                  return <MenuItem value={index}>{time}</MenuItem>
                                })
                              }
                          </Select>
                        </Box>
                      </Box>
                    )
                  )
                )
              }
              <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                  <Button color="inherit" onClick={handleBack} sx={{ mr: 1 }} >{activeStep === 0? "Cancel" :"Back"}</Button>
                  <Box sx={{ flex: '1 1 auto' }} />
                  <Button onClick={handleNext}>{activeStep === 3 ? 'Publish' : 'Next'}</Button>
              </Box>
              </React.Fragment>
            }
          </Box>

    </ThemeProvider>
  )
}

export default AddComplaint